import { MenuBookOutlined } from '@mui/icons-material'
import React from 'react'
import { Link } from 'react-router-dom'
import './Navbar.css';

const Navbar = (props) =>{
  console.log(`props`,props)
  const handleScroll = (ref) => {
    console.log(ref)
    ref.current.scrollIntoView({ behavior: 'smooth' });
  }
  return (
    <div className='navbar'>
      <div className='nav-logo'>
        <h2>Ritik</h2>
      </div>
      <nav>
      <Link to='/' onClick={() => handleScroll(props.homeRef)}>Home</Link>
      <Link to='/about' onClick={() => handleScroll(props.aboutRef)}>About</Link>
      <Link to='/proficiencies' onClick={() => handleScroll(props.proficienciesRef)}>Proficiencies</Link>
      <Link to='/projects' onClick={() => handleScroll(props.projectsRef)}>Projects</Link>
      <Link to='/contact' onClick={() => handleScroll(props.contactRef)}>Contact</Link>
      <Link to='/resume' target='_blank' rel='noopener noreferrer'><button><span>Resume</span> <i className="fas fa-file-download"></i></button></Link>
      </nav>
      <div className='changer'>
        <MenuBookOutlined className='menu'/>
      </div>
    </div>
  )
}

export default Navbar