import React, { forwardRef } from 'react'
import './Proficiencies.css'

const Proficiencies = forwardRef((props,ref) => {
    return (
        <div ref={ref} id='proficiencies' className='proficiencies section'>
            <h1>Proficiencies</h1>
            <div className='icon-container'>
            <div className='icon'>
            <i className="devicon-html5-plain colored techstack-icon"></i>
            <span>HTML</span>
            </div>
            <div className='icon'>
                <i className="devicon-css3-plain colored techstack-icon"></i>
                <span>CSS</span>
            </div>
            <div className='icon'>
                <i className="devicon-javascript-plain colored techstack-icon"></i>
                <span>JavaScript</span>
            </div>
            <div className='icon'>
                <i className="devicon-react-original colored techstack-icon"></i>
                <span>React</span>
            </div>
            <div className='icon'>
                <i className="devicon-redux-original colored techstack-icon"></i>
                <span>Redux</span>
            </div>
            <div className='icon'>
                <i className="devicon-nodejs-plain colored techstack-icon"></i>
                <span>Node</span>
            </div>
            <div className='icon'>
                <i className="devicon-express-original colored techstack-icon"></i>
                <span>Express</span>
            </div>
            <div className='icon'>
                <i className="devicon-mongodb-plain colored techstack-icon"></i>
                <span>MongoDB</span>
            </div>
            <div className='icon'>
                <i className="devicon-typescript-plain colored techstack-icon"></i>
                <span>TypeScript</span>
            </div>
            </div>
        </div>
    )
})

export default Proficiencies